/* Navbar Style Start */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 85.333px;
  background: var(--white);
  box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  animation: fadeIn 0.5s ease-in-out;
} 

.navbar--items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
  animation: slideInFromTop 0.5s ease-in-out forwards;
}
.navbar--items ul > li > a {
  text-decoration: none;
}
/* Navbar Content */
.navbar--content {
  color: var(--darkblue);
  font-size: 20px;
  font-style: normal;
  font-weight: 200;
  line-height: 24px;
  cursor: pointer;
}

.navbar--active-content {
  color: var(--primary);
  font-weight: 700;
}

.navbar--logo {
  display: flex;
  align-items: flex-start;
  animation: slideInFromTop 0.5s ease-in-out forwards;
}

.navbar--logo img {
  height: 50px;
  cursor: pointer;
}

.dark .navbar--logo {
  border: 0px solid black;
}

/* Switch styles */
.switch {
  position: relative;
  display: inline-block;
  width: 62px;
  height: 26px;
  top: 4px;
  animation: slideInFromTop 0.5s ease-in-out forwards;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  transition: 0.4s;
  border-radius: 34px;
}

.light-mode-round {
  border: 1px solid orange;
}

.dark-mode-round {
  background-color: black;
  border: 1px solid yellow;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 11px;
  bottom: 5px;
  /* background-color: white; */
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #000000;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 50px;
}

.slider.round:before {
  border-radius: 60%;
}

/* Icon styles */
.dark-mode-icon,
.light-mode-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 18px;
}

.dark-mode-icon {
  right: 10px;
  color: yellow;
}

.light-mode-icon {
  left: 10px;
  color: orange;
}

/* Navbar Styler Ends */

